<!--  -->
<template>
  <div class='mobile-container'>
    <el-header style="background-color:#409EFF;">
      <div class="mobile-header__wrap">
        <el-image class="logo" :src="logoUrl" fit="contain"></el-image>
        <span style="font-size:24px; color:white;font-family:'common-Regular'"></span>
        <!-- <span style="font-size:16px;color:white;font-family:'common-Regular'"></span> -->
      </div>
    </el-header>
    <div id="antvMap" style="width:100%;height:100%;position: absolute; z-index: 1;"></div>
    <ul class="buttonMenu" style="z-index:2">
      <li :class="{'activeButton':activeModel===1}" @click="touchModel(1)">
        <i class="el-icon-menu"></i>
        <div></div>
      </li>
      <li class="centerButton" :class="{'activeButton':activeModel===2}" @click="touchModel(2)">
        <i class="el-icon-menu"></i>
        <div></div>
      </li>
      <li :class="{'activeButton':activeModel===3}" @click="touchModel(3)">
        <i class="el-icon-menu"></i>
        <div></div>
      </li>
    </ul>
    <MRunningHelp :drawer="activeModel===1" @drawrun="drawRunning" />
    <MRoutePlan :drawer="activeModel===2" @draw="drawLine" />
    <MDateLog :drawer="activeModel===3" @drawdate="drawDate" />
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import { AntvMap } from "../../static/Plugins/Script/antvMap.js";
import MRoutePlan from "../components/MRoutePlan.vue";
import MDateLog from "../components/MDateLog.vue";
import MRunningHelp from "../components/MRunningHelper.vue";

export default {
  //import
  components: {
    MRoutePlan,
    MDateLog,
    MRunningHelp,
  },
  data() {
    //
    return {
      activeModel: 0,
      AntvMapInstance: null,
      logoUrl: "../../static/Source/Images/logo.png",
      currentLayerName: null,
      polygonlayerName: "",
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    init() {
      this.AntvMapInstance = new AntvMap("antvMap");
      this.AntvMapInstance.loadMap();
    },
    // loadMap() {
    //   console.log("");
    //   console.log(
    //     navigator.userAgent.match("Win") ? "windows" : ""
    //   );
    //   LoadMap("antvMap");
    // },
    touchModel(value) {
      this.activeModel = value;
    },

    // 
    drawLine(data) {
      console.log(data);
      // 
      if (this.currentLayerName != null) {
        if (typeof this.currentLayerName == "string") {
          this.AntvMapInstance.removeLayer(this.currentLayerName);
        } else {
          for (let i in this.currentLayerName) {
            this.AntvMapInstance.removeLayer(this.currentLayerName[i]);
          }
        }
      }
      // 
      this.AntvMapInstance.addMarker(data.point);
      this.currentLayerName = [data.point.name];

      // 
      if ("polygon" in data) {
        this.AntvMapInstance.addPolygon(data.polygon);
        this.currentLayerName.push(data.polygon.name);
        this.polygonlayerName = data.polygon.name;
      }
      // 
      else {
        this.AntvMapInstance.removeLayer(this.polygonlayerName);
      }

      // 
      if (data.line.name == "walk" || data.line.name == "noparking") {
        this.AntvMapInstance.addLine(data.line);
        this.currentLayerName.push(data.line.name);
      } else {
        // 
        this.AntvMapInstance.addLine(data.line.one);
        this.AntvMapInstance.addLine(data.line.two);
        this.currentLayerName.push(data.line.one.name);
        this.currentLayerName.push(data.line.two.name);
      }
    },

    // 
    drawRunning(data) {
      // 
      if (this.currentLayerName != null) {
        if (typeof this.currentLayerName == "string") {
          this.AntvMapInstance.removeLayer(this.currentLayerName);
        } else {
          for (let i in this.currentLayerName) {
            this.AntvMapInstance.removeLayer(this.currentLayerName[i]);
          }
        }
      }
      // 
      this.AntvMapInstance.addLine(data.line);
      this.AntvMapInstance.addMarker(data.point);
      this.currentLayerName = [data.line.name, data.point.name];
    },

    //
    drawDate(data) {
      // 
      this.removeAll();

      this.AntvMapInstance.addLine(data);
      this.currentLayerName = data.name;
    },

    removeAll() {
      if (this.currentLayerName != null) {
        if (typeof this.currentLayerName == "string") {
          this.AntvMapInstance.removeLayer(this.currentLayerName);
        } else {
          for (let i in this.currentLayerName) {
            this.AntvMapInstance.removeLayer(this.currentLayerName[i]);
          }
        }
      }
    },
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    // this.loadMap();
    this.init();
  },
  // - 
  beforeCreate() {},
  // - 
  beforeMount() {},
  // - 
  beforeUpdate() {},
  // - 
  updated() {},
  // - 
  beforeDestroy() {},
  // - 
  destroyed() {},
  //keep-alive
  activated() {},
};
</script>
<style scoped>
.mobile-container {
  width: 100%;
  height: 100%;
}

.mobile-header__wrap {
  display: flex;
  /* margin-top: 4.5%; */
  padding: 15px;
  justify-content: center;
}

.logo {
  position: absolute;
  left: 10px;
  top: -2px;
  padding: 0;
  width: 80px;
  height: 80px;
}

.buttonMenu {
  position: absolute;
  bottom: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 50px;
  text-align: center;
  background-color: #fff;
  border-radius: 10px 10px 0 0;
}

.buttonMenu li {
  display: inline-block;
  list-style-type: none;
}

.centerButton {
  margin: 0 35px;
}
.activeButton {
  color: rgba(0, 162, 255, 0.974);
}
</style>


