<!--  -->
<template>
  <div
    id="ropute-plan"
    class="navigator-card"
    style="position: absolute; display: none; left: 75%; top: 1%; z-index: 100"
  >
    <el-card class="box-card" shadow="always">
      <div slot="header" class="clearfix">
        <span></span>
        <el-button
          style="float: right; padding: 3px 0"
          type="text"
          @click="onClose"
          ></el-button
        >
      </div>
      <div class="text item">
        <el-row>
          <el-autocomplete
            class="inline-input"
            v-model="navigatorForm.origin"
            :fetch-suggestions="querySearch"
            :placeholder="originText"
            @select="handleSelect"
          ></el-autocomplete>
          <el-autocomplete
            class="inline-input"
            v-model="navigatorForm.target"
            :fetch-suggestions="querySearch"
            :placeholder="targetText"
            @select="handleSelect"
          ></el-autocomplete>
        </el-row>
        <el-row style="margin-top: 15px">
          <el-select
            v-model="PathOptions.value"
            :disabled="PathOptions.disable"
            placeholder=""
            style="width: 202px"
            @change="pathChange"
          >
            <el-option
              v-for="item in PathOptions.options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-row>
        <el-row style="margin-top: 25px">
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-row>
      </div>
    </el-card>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

import axios from "axios";

axios.defaults.baseURL = "http://172.27.145.91:9011";

let pointer = null;

export default {
  //import
  components: {},
  data() {
    //
    return {
      originText: "",
      targetText: "",

      selectPathList: [],

      originText: "",
      targetText: "",

      navigatorForm: {
        origin: "",
        target: "",
      },

      PathOptions: {
        options: [
          { label: "", value: "walk" },
          { label: "-", value: "noparking" },
          { label: "--", value: "inside" },
          { label: "--", value: "outside" },
        ],
        value: "",
        disable: true,
      },

      resultFromServer: null,
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    init() {
      pointer = this;
    },

    // 
    querySearch(queryString, cb) {
      var list = this.selectPathList;
      var results = queryString
        ? list.filter(this.createFilter(queryString))
        : list;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return (list) => {
        return (
          list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    loadAll() {
      return this.$store.state.placeList;
    },
    handleSelect(item) {
      console.log(item);
    },

    // 
    refresh() {
      this.navigatorForm.origin = "";
      this.navigatorForm.target = "";

      // 
      if (this.$store.state.auto_orient) {
        this.originText = "...";
        this.getLocation();
      } else {
        this.originText = "";
      }
    },

    getLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (data) => {
            // todo 
            console.log(data);
          },
          (error) => {
            console.log(error);
            // 
            pointer.origin = "";
          },
          {
            enableHighAccuracy: true,
            timeout: 3000,
          }
        );
      } else {
        this.$confirm(
          "''",
          "",
          {
            confirmButtonText: "",
            type: "error",
            center: true,
          }
        );
      }
    },

    onShow() {
      this.refresh();
      document.getElementById("ropute-plan").style.display = "block";
    },

    onSubmit() {
      if (this.navigatorForm.origin != "" && this.navigatorForm.target != "") {
        // todo
        this.AxiosRequest({
          origin: this.navigatorForm.origin,
          target: this.navigatorForm.target,
        });
      } else {
        this.$notify({
          title: "",
          message: "",
          type: "warning",
        });
      }
    },

    onClose() {
      this.PathOptions.disable = false;
      document.getElementById("ropute-plan").style.display = "none";
      this.$emit('close');
    },

    pathChange(val) {
      switch (val) {
        case "walk":
          this.$emit("draw", {
            line: {
              name: val,
              size: 2,
              color: "green",
              interval: 0.6,
              trailLength: 1.5,
              duration: 1.5,
              data: [{ path: pointer.resultFromServer.walk.path }],
            },
            point: {
              name: "points",
              size: 20,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
          });
          break;
        case "noparking":
          this.$emit("draw", {
            line: {
              name: val,
              size: 2,
              color: "blue",
              interval: 0.6,
              trailLength: 1.5,
              duration: 1.5,
              data: [{ path: pointer.resultFromServer.noparking.path }],
            },
            point: {
              name: "points",
              size: 20,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
          });
          break;
        case "inside":
          this.$emit("draw", {
            line: {
              name: "drive",
              one: {
                name: val + "car",
                size: 2,
                color: "blue",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.inside.carpath }],
              },
              two: {
                name: val + "walk",
                size: 2,
                color: "green",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.inside.walkpath }],
              },
            },
            point: {
              name: "points",
              size: 20,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
            polygon: {
              name: pointer.PathOptions.value,
              color: "#B1DDF0",
              size: 2,
              data: [{ path: pointer.resultFromServer.inside.polygon }],
            },
          });
          break;
        case "outside":
          this.$emit("draw", {
            line: {
              name: "drive",
              one: {
                name: val + "car",
                size: 2,
                color: "blue",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.outside.carpath }],
              },
              two: {
                name: val + "walk",
                size: 2,
                color: "green",
                interval: 0.6,
                trailLength: 1.5,
                duration: 1.5,
                data: [{ path: pointer.resultFromServer.outside.walkpath }],
              },
            },
            point: {
              name: "points",
              size: 20,
              data: [
                {
                  id: "001",
                  name: "start",
                  lon: this.resultFromServer.origin[0],
                  lat: this.resultFromServer.origin[1],
                },
                {
                  id: "002",
                  name: "stop",
                  lon: this.resultFromServer.target[0],
                  lat: this.resultFromServer.target[1],
                },
              ],
            },
          });
          break;
        default:
          break;
      }
    },

    AxiosRequest(data) {
      console.log(data);
      axios
        .get("/routePlan", {
          params: {
            origin: data.origin,
            target: data.target,
          },
        })
        .then((res) => {
          console.log(res);
          pointer.resultFromServer = res.data;
          pointer.PathOptions.disable = false;
          pointer.PathOptions.value = "walk";
          let draw = {
            name: pointer.PathOptions.value,
            size: 2,
            color: "green",
            interval: 0.6,
            trailLength: 1.5,
            duration: 1.5,
            data: [{ path: pointer.resultFromServer.walk.path }],
          };
          let points = {
            name: "points",
            size: 20,
            data: [
              {
                id: "001",
                name: "start",
                lon: this.resultFromServer.origin[0],
                lat: this.resultFromServer.origin[1],
              },
              {
                id: "002",
                name: "stop",
                lon: this.resultFromServer.target[0],
                lat: this.resultFromServer.target[1],
              },
            ],
          };
          this.$emit("draw", {
            line: draw,
            point: points,
          });
        });
    },
  },
  // - this
  created() {
    this.init();
  },
  // - DOM
  mounted() {
    this.selectPathList = this.loadAll();
  },
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {}, //keep-alive
};
</script>
<style scoped>
/*  */
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 16px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 450px;
}
</style>


