<!--  -->
<template>
    <div id="running-helper" class='running-helper' style="position:absolute;display: none;
    left: 77%;top: 1%; z-index: 100;">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>{{title}}</span>
                <el-button style="float: right; padding: 3px 0" type="text" @click="onClose"></el-button>
            </div>
            <div class="text item">
                <el-form ref="running_form" :model="RunningForm" label-width="80px">
                    <el-form-item label="" prop="origin">
                        <el-autocomplete
                        class="inline-input"
                        v-model="RunningForm.origin"
                        :fetch-suggestions="querySearch"
                        placeholder=""
                        @select="handleSelect"
                        ></el-autocomplete>
                    </el-form-item>
                    <el-form-item label="" prop="target">
                        <el-autocomplete
                        class="inline-input"
                        v-model="RunningForm.target"
                        :fetch-suggestions="querySearch"
                        placeholder=""
                        @select="handleSelect"
                        ></el-autocomplete>
                    </el-form-item>
                    <el-form-item label="" prop="num">
                        <el-input-number v-model="RunningForm.num" :step="0.5"></el-input-number>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-card>
    </div>
</template>

<script>
//jsjsjson
//import  from '';
import axios from 'axios'

let pointer = null;

export default {
//import
components: {},
data() {
//
return {
    selectList:[],

    title:'',
    isSubmit:false,
    RunningForm:{
        origin:'',
        target:'',
        num:1.5
    },

    resultFromServer:null
};
},
// data
computed: {},
//data
watch: {},
//
methods: {
    onShow(){
        document.getElementById('running-helper').style.display = 'block';
    },

    onClose(){
        this.refresh();
        document.getElementById('running-helper').style.display = 'none';
        this.$emit('close');
    },

    onSubmit(){
        if(this.RunningForm.origin != '' && this.RunningForm.target != ''){
            // todo
            this.AxiosRequest({
                origin:this.RunningForm.origin,
                target:this.RunningForm.target,
                length:this.RunningForm.num
            });
        }else{
            this.$notify({
                title: '',
                message: '',
                type: 'warning'
            });
        }
    },

    refresh(){
        this.title = "";
        this.RunningForm.origin = '',
        this.RunningForm.target = '',
        this.RunningForm.num = 1.5
    },

    AxiosRequest(data){
        axios.defaults.baseURL = 'http://172.27.145.91:9011';
        axios.get('/runAssistant',{
            params:{
                origin:data.origin,
                target:data.target,
                length:data.length
            }
        }).then(res=>{
            console.log(res);
            pointer.resultFromServer = res.data;
            // 
            pointer.title = `:  ${pointer.resultFromServer.distance / 1000} km`;
            let draw = {
                name:'running',
                size:2,
                color:"red",
                interval:1.5,
                trailLength:1,
                duration:5,
                data:[{path:pointer.resultFromServer.path}]
            };
            let points = {
                name:'points',
                size:20,
                data:[
                    {
                        id:"001",
                        name:"start",
                        lon:this.resultFromServer.origin[0],
                        lat:this.resultFromServer.origin[1]
                    },
                    {
                        id:"002",
                        name:"stop",
                        lon:this.resultFromServer.target[0],
                        lat:this.resultFromServer.target[1]
                    },
                    {
                        id:"003",
                        name:"pounch",
                        lon:this.resultFromServer.pounch1.coordinate[0],
                        lat:this.resultFromServer.pounch1.coordinate[1]
                    },
                    {
                        id:"004",
                        name:"pounch",
                        lon:this.resultFromServer.pounch2.coordinate[0],
                        lat:this.resultFromServer.pounch2.coordinate[1]
                    },
                ]
            };
            this.$emit('drawrun',{
                line: draw,
                point: points
            });
        })
    },



    // 
    querySearch(queryString, cb) {
        var list = this.selectList;
        var results = queryString ? list.filter(this.createFilter(queryString)) : list;
        //  callback 
        cb(results);
    },
    createFilter(queryString) {
        return (list) => {
          return (list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
    },
    loadAll(){
        return this.$store.state.placeList
    },
    handleSelect(item){
        console.log(item)
    }
},
// - this
created() {
    pointer = this;
},
// - DOM
mounted() {
    this.selectList = this.loadAll();
},
beforeCreate() {}, // - 
beforeMount() {}, // - 
beforeUpdate() {}, // - 
updated() {}, // - 
beforeDestroy() {}, // - 
destroyed() {}, // - 
activated() {}, //keep-alive
}
</script>
<style scoped>
    .text {
        font-size: 14px;
    }

    .item {
        margin-bottom: 18px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 400px;
    }


</style>


