<!--  -->
<template>
  <div class='container'>
    <el-drawer class="menuDrawer" title="" :visible.sync="mydrawer" :with-header="false" :modal="false" direction="btt" :size="isSearching?'20%':'50%'">
      <div v-show="!isSearching">
        <el-form ref="runningForm" :model="RunRoute">
          <el-form-item class="routeInput">
            <el-autocomplete class="input" placeholder="" v-model="RunRoute.start" :fetch-suggestions="querySearch">
              <div slot="prepend"></div>
            </el-autocomplete>
            <el-autocomplete class="input" placeholder="" v-model="RunRoute.target" :fetch-suggestions="querySearch">
              <div slot="prepend"></div>
            </el-autocomplete>
          </el-form-item>
          <el-form-item>
            <el-input-number class="numInput" size="medium" v-model="RunRoute.length" :precision="1" :step="0.1" :min="1.5" :max="3"></el-input-number>
            <el-button class="button" type="primary" size="medium" @click="submitPoint()"></el-button>
          </el-form-item>
        </el-form>

        <el-image class="weatherImage" :src="weather.weatherUrl" fit="contain">
        </el-image>
        <el-descriptions title="" :column="2" class="weaInfo">
          <el-descriptions-item label="">{{weather.wea}}</el-descriptions-item>
          <el-descriptions-item label="">{{weather.air}}</el-descriptions-item>
          <el-descriptions-item label="">{{weather.temperature+""}}</el-descriptions-item>
          <el-descriptions-item label="">{{weather.humidity}}</el-descriptions-item>
        </el-descriptions>
      </div>
      <div class="getRoute" v-show="isSearching">
        <el-page-header @back="goBack" content=""></el-page-header>
        <el-descriptions class="margin-top" :column="2">
          <el-descriptions-item label="1">{{passList[0]}}</el-descriptions-item>
          <el-descriptions-item label="2">{{passList[1]}}</el-descriptions-item>
        </el-descriptions>
        <div class="buttonDiv">
          <span>{{""+length+""}}</span>
          <div class="btn">
            <el-button class="followBtn" type="primary" size="mini" @click="onSubmit"></el-button>
            <!-- <el-button class="followBtn" type="primary" size="mini"></el-button> -->
          </div>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import axios from "axios";

let pointer = null;

export default {
  //import
  components: {},
  props: ["drawer"],

  data() {
    //
    return {
      mydrawer: this.drawer,
      isSearching: false,
      RunRoute: {
        start: "",
        target: "",
        length: 1.5,
      },
      passList: ["", ""],
      length: 1.5,
      weather: {
        wea: "",
        temperature: 21,
        air: 0,
        humidity: "10%",
        weatherUrl: "../../static/Source/Images/qing.png",
      },
      selectList: [],
      resultFromServer: null,
    };
  },
  // data
  computed: {},
  //watchdatawatch 
  watch: {
    drawer: function (newVal, oldVal) {
      this.mydrawer = newVal;
    },
  },
  // - this
  created() {
    pointer = this;
  },
  // - DOM
  mounted() {
    this.getWeather();
    this.selectList = this.loadAll();
  },
  // - 
  beforeCreate() {},
  // - 
  beforeMount() {},
  // - 
  beforeUpdate() {},
  // - 
  updated() {},
  // - 
  beforeDestroy() {},
  // - 
  destroyed() {},
  //keep-alive
  activated() {},
  //
  methods: {
    submitPoint() {
      if (this.RunRoute.start === "" || this.RunRoute.target === "") {
        return;
      }
      this.isSearching = true;
      this.onSubmit();
    },
    goBack() {
      this.isSearching = false;
    },
    getWeather() {
      var newWeather;
      this.$axios
        .get(
          "https://www.tianqiapi.com/free/day?appid=75698768&appsecret=7LyHELsc&city="
        )
        .then((reponse) => {
          this.weather.temperature = reponse.data.tem;
          this.weather.air = reponse.data.tem;
          this.weather.humidity = reponse.data.humidity;
          this.weather.wea = reponse.data.wea;
          this.weather.weatherUrl =
            "../../static/Source/Images/" + reponse.data.wea_img + ".png";
        })
        .catch((err) => {
          console.log(err);
        });
    },
    loadAll() {
      return this.$store.state.placeList;
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    querySearch(queryString, cb) {
      var restaurants = this.selectList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      //  callback 
      cb(results);
    },

    onSubmit() {
      if (this.RunRoute.start != "" && this.RunRoute.target != "") {
        // todo
        this.AxiosRequest({
          origin: this.RunRoute.start,
          target: this.RunRoute.target,
          length: this.RunRoute.length,
        });
      } else {
        this.$notify({
          title: "",
          message: "",
          type: "warning",
        });
      }
    },

    AxiosRequest(data) {
      axios.defaults.baseURL = "http://172.27.145.91:9011";
      axios
        .get("/runAssistant", {
          params: {
            origin: data.origin,
            target: data.target,
            length: data.length,
          },
        })
        .then((res) => {
          pointer.resultFromServer = res.data;
          // 
          pointer.length = pointer.resultFromServer.distance / 1000;
          this.passList[0] = pointer.resultFromServer.pounch1.name;
          this.passList[1] = pointer.resultFromServer.pounch2.name;
          let draw = {
            name: "running",
            size: 2,
            color: "red",
            interval: 1.5,
            trailLength: 1,
            duration: 5,
            data: [{ path: pointer.resultFromServer.path }],
          };
          let points = {
            name: "points",
            size: 10,
            data: [
              {
                id: "001",
                name: "start",
                lon: this.resultFromServer.origin[0],
                lat: this.resultFromServer.origin[1],
              },
              {
                id: "002",
                name: "stop",
                lon: this.resultFromServer.target[0],
                lat: this.resultFromServer.target[1],
              },
              {
                id: "003",
                name: "pounch",
                lon: this.resultFromServer.pounch1.coordinate[0],
                lat: this.resultFromServer.pounch1.coordinate[1],
              },
              {
                id: "004",
                name: "pounch",
                lon: this.resultFromServer.pounch2.coordinate[0],
                lat: this.resultFromServer.pounch2.coordinate[1],
              },
            ],
          };
          this.$emit("drawrun", {
            line: draw,
            point: points,
          });
        });
    },
  },
};
</script>
<style scoped>
.container {
  width: 100%;
  height: 100%;
}
.menuDrawer {
  position: absolute;
  left: 0%;
  bottom: 50px;
  overflow: hidden;
}

.routeInput .input {
  margin: 10px 10px;
  width: 90%;
}

.numInput {
  margin: 0 10px;
}

.button {
  margin-left: 50px;
}

.weatherImage {
  float: left;
  margin-left: 10px;
  margin-top: 10px;
  width: 40%;
  height: 100px;
}

.weaInfo {
  margin-top: 10px;
  float: right;
  width: 50%;
}

.getRoute {
  margin: 10px 10px;
}

.routeContent {
  position: relative;
  margin: 10px 20px;
}
.margin-top {
  margin: 10px 10px;
}
.buttonDiv {
  margin-left: 10px;
}
.btn {
  display: inline-block;
  margin-left: 50px;
}
</style>


